import React from 'react';

import classes from './PortfolioItem.css';

const portfolioItem = (props) => (
  <article className={classes.Item} style={{backgroundImage: `url(${props.image})`}}>
    <div className={classes.ItemInfoContainer}>
      <h1 className={classes.ItemTitle}>{props.name}</h1>
      <span className={classes.ItemDescription}>
        <p><strong>Technology</strong>: {props.technology}</p>
        <p><strong>Goal</strong>: {props.goal}</p>
        <p>
          <button className={classes.ButtonLink}
                  onClick={props.clicked}>
            <strong>Check it out</strong>
          </button>
        </p>
        </span>
    </div>
  </article>
);

export default portfolioItem;
